import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpService } from '../../app/http.service';
import { DomSanitizer } from '@angular/platform-browser';
import {SocketService} from "../../app/socket.service";
declare var $: any;

@Component({
  selector: 'app-votingdetails',
  styleUrls:['./meetingdetails.css'],
  templateUrl: './votingdetails.component.html'
})
export class VotingdetailsComponent implements OnInit {
	meeting_object: any;
	voting_object
	meetObjLoaded = false;
	notes = [];
	new_reply = '';
	bread_crumb = {
		items: [],
		title: ''
	};
	next = '';
	prev = '';
	meeting_type = 'upcoming';
	title = '';
	flag = '';
	first_time = true;
	me: any;
	conference_not_active = false;

	constructor(private route: ActivatedRoute,
				private router: Router,
				private httpService: HttpService,
				private sanitizer: DomSanitizer,
				private socketService: SocketService) {
		this.meeting_object = {};
		this.route.params.subscribe(params => this.get_data());
	}

	get_data() {
		this.bread_crumb = {
			items: [],
			title: ''
		};
		const page_url = window.location + '';
		let req_peram = (window.location + '').split('/');
		this.flag = req_peram[req_peram.length - 3];
		if (['upcoming', 'completed', 'archived'].indexOf(this.flag) === -1) {
			this.flag = '';
		}
		
		const obj_this = this;
        const input_data = {id: this.route.snapshot.params.id, meeting_type: this.flag};
        
        let on_data = function(result) {
            try {				
                if(result.message)
                {
                    $('#main-div').html('<h2 style="text-align:center">'+result.message+'</h2>');
                    return;
				}
				obj_this.voting_object = result.voting;                
				obj_this.meetObjLoaded = true;
                obj_this.voting_object.model = 'meeting_point.voting';                
				make_bread_crumb(obj_this.voting_object.name);				
            } catch (er) {
                console.log(er);
            }			
        };
		let args = {
            app: 'voting',
            model: 'Voting',
            method: 'get_details'
        }			
        let final_input_data = {
            params: input_data,
            args: args
        };
        obj_this.httpService.get(final_input_data, on_data, null);
		function make_bread_crumb(page_title) {
			const bread_crumb_items = obj_this.bread_crumb.items;
			if(obj_this.voting_object.topic.name)
			{
				bread_crumb_items.push({title: obj_this.voting_object.topic.name, link: '/topic/'+obj_this.voting_object.topic.id});
			}
			else if(obj_this.voting_object.meeting.name)
			{
				bread_crumb_items.push({title: obj_this.voting_object.meeting.name, link: '/home/meeting/'+obj_this.voting_object.meeting.id});
			}
			// else
			// {
			// 	bread_crumb_items.push({title: 'Home', link: '/'});
			// }
			if (page_title) {
				obj_this.bread_crumb.title = page_title;
			}
		}
	}

	isInProgress() {
		// var startTime = moment('8:45am', 'h:mma');
		// var endTime = moment('9:00am', 'h:mma').add(3, 'hours');
		// var now = moment();
		// if(now.isBefore(endTime) && now.isAfter(startTime))
		// console.log('In Progress!!!')
		// else
		// console.log('Finished!!')
		return true;
	}
	
	voting_closed(close_date: string){
		let closed = false;
		let closingDate = new Date(close_date).getTime();
		let dateNow = new Date().getTime();
		if (closingDate < dateNow){
			closed = true;
		}
		return closed;
	}

	voting_opened(open_date: string){
		let opened = false;
		let openingDate = new Date(open_date).getTime();
		let dateNow = new Date().getTime();
		if (openingDate > dateNow){
			opened = true;
		}
		return opened;
	}

	respond_invitation(option_name: string, response: number, voting_id: number) {
		
        let chek = $('.upcomingButton .fa-check:first');
        if(chek.length > 0)
        {
            let c_name = chek.parent().text();
            if(c_name == option_name)
            {
                return;
            }
        }
        let req_url =  '/voting/submit';
		let obj_this = this;
		let input_data = {
			voting_id: voting_id,
			voting_option_id: response			
		};
		if(!obj_this.voting_opened(obj_this.voting_object.open_date))
		{
			if(!obj_this.voting_closed(obj_this.voting_object.close_date))
			{
				if (obj_this.voting_object.signature_required)
				{
					window['voting_id'] = voting_id;
					$('.strt_sign').trigger('click');
					window['voting_option_id'] = response;
					window['on_vote_sign_saved'] = function(signature_data){            
						obj_this.voting_object.my_status = option_name;
						obj_this.voting_object.signature_data = signature_data;
					}
				}
				else
				{
					let args = {
                        app: 'voting',
                        model: 'VotingAnswer',
                        method: 'submit'
                    }			
                    let final_input_data = {
                        params: input_data,
                        args: args
                    };
                    obj_this.httpService.get(final_input_data, function(){
						obj_this.voting_object.my_status = option_name;
					}, function(){
						obj_this.voting_object.signature_required = true;
					});
				}                
			}
			else
			{
				alert('This Approval/Voting is Closed now.');
			}
		}
		else
		{
			alert('This Approval/Voting is not Opened yet.')
		}
	}	

	ngOnInit() {
		if(!window['sign_loaded'] && $('.strt_sign').length == 0)
        {
			window['sign_loaded'] = 1;
            $('body').append('<script src="/static/assets/annotator/js/dn_sign.js"></script>');
		}
	}

	ngOnDestroy() {
        window['voting_id'] = -1;
	}
}
