﻿import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { HttpService } from '../../app/http.service';
import { ActivatedRoute } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

declare var $:any;

@Component({
	styleUrls:['./profiledetails.css'],
	templateUrl: 'profiledetails.component.html'
})
export class ProfileDetailsComponent implements OnInit {
	edit_mode: boolean;
	my_profile = true;
	last_login = {
		last: {
			login_time: '',
			platform: '',
			browser: '',
			ip: '',
			location: ''
		},
		second_last: {
			login_time: '',
			platform: '',
			browser: '',
			ip: '',
			location: ''
		}
	};
	profile_data = {
		login: this.last_login,
	};
	modified_profile_data = {};
	submitted = false;
	admin_info = false;
	next = '';
	prev = '';

	constructor(private httpService: HttpService, private formBuilder: FormBuilder, private route: ActivatedRoute, private sanitizer: DomSanitizer) {
		this.edit_mode = false;
		this.route.params.subscribe(params => this.get_data());
	}

	editProfile() {
		this.edit_mode = !this.edit_mode;
	}

	addFile(event, filter){
		const obj_this = this;
		var element = event.target;
		console.log(element)
		var file = element.files[0];
		var fileReader = new FileReader();
		fileReader.readAsDataURL(file);
		fileReader.onload = function () {
			if(filter === 'profile'){
				obj_this.profile_data['image_medium'] = fileReader.result;
				obj_this.modified_profile_data['image_medium'] =fileReader.result;
			}
			else if(filter === 'admin'){
				obj_this.profile_data['admin_image'] = fileReader.result;
				obj_this.modified_profile_data['admin_image'] =fileReader.result;
			}
			else{
				obj_this.modified_profile_data['resume'] =fileReader.result;
			}
		};
		fileReader.onerror = function (error) {
			console.log('Error: ', error);
		};
	}

	bio_html = undefined;
	get_data() {
        let id = this.route.snapshot.params.id;
		// this.bread_crumb_items = this.httpService.make_bread_crumb();
        let input_data = undefined;
        if (id) {
			this.my_profile = false;
			input_data = { id: id};
        }
        else
        {
            input_data= {};
        }
		let args = {
            app: 'meetings',
            model: 'ManagerDirector',
            method: 'get_details'
        }			
        input_data = {
            params: input_data,
            args: args
        }; 

		const obj_this = this;
		const req_url = '/ws/profile-json';		
		const success_cb = function (result) {
			if(result.profile.admin_email || result.profile.admin_cell_phone
				|| result.profile.admin_fax || result.profile.admin_work_phone
				|| result.profile.admin_image || result.profile.admin_first_name
				|| result.profile.admin_last_name || result.profile.admin_nick_name
			)
			{
				obj_this.admin_info = true;
			}
			if(result.profile.image_medium)
			{
				var d = Date.now();
				result.profile.image_medium += '?a='+d;
				$('#navbar-profile-img').attr('src', result.profile.image_medium);
				//console.log(result.profile.image_medium, 111);
			}
			if(result.profile.signature_img)
			{
				var d = Date.now();
				result.profile.signature_img += '?a='+d;
			}

			for(var key in result.profile){
				if(key == 'resume'){
					localStorage.setItem('resume', result.profile[key]);
				}
				obj_this.profile_data[key] = result.profile[key];
			}
			if(result.profile.bio)
			{
				obj_this.bio_html = obj_this.sanitizer.bypassSecurityTrustHtml(result.profile.bio);				
			}
		};
		const failure_cb = function (error) {
		};
		this.httpService.get(input_data, success_cb, failure_cb);
	}

	onSubmit() {
		this.submitted = true;
		const obj_this = this;
		const form_data = obj_this.modified_profile_data;
		const input_data = {};
		for (const key in form_data) {
			if(obj_this.modified_profile_data[key] != '')
				input_data[key] = obj_this.modified_profile_data[key];
			obj_this.modified_profile_data[key] = '';
        }
        let args = {
            app: 'meetings',
            model: 'User',
            method: 'update_profile'
        }			
        let final_input_data = {
            params: input_data,
            args: args
        };
		this.httpService.post(final_input_data,
			(data: any) => {
				obj_this.edit_mode = false;
				const x = document.getElementById('slot-select-success');
				x.className = 'snackbar-success show';
				setTimeout(function () {
					x.className = x.className.replace('show', '');
				}, 3000);
				this.get_data();
			},
			(error) => {
				const x = document.getElementById('slot-select-error');
				x.className = 'snackbar-error show';
				setTimeout(function () {
					x.className = x.className.replace('show', '');
				}, 3000);
			});
	}

	ngOnInit(){		
        if(!window['sign_loaded'] && $('.strt_sign').length == 0)
        {
			window['sign_loaded'] = 1;
            $('body').append('<script src="/static/assets/annotator/js/dn_sign.js"></script>');
		}
		
	}

	ngOnChanges(){
        console.log("ngOnChanges")
	}
	ngDoCheck(){
		// this.profile_data["signature_img"]=this.profile_data["signature_img"]+"&h="+ Math.random();
        // console.log("ngDoCheck",this.profile_data["signature_img"])
	}
	
}
