import { Component, OnInit} from '@angular/core';
import { Router } from "@angular/router";
declare var $: any;

@Component({
    selector: 'app-jitsi',
    templateUrl: './jitsi.component.html'
})
export class JitsiComponent implements OnInit {

    constructor(private router: Router) {

    }

    ngOnInit() {
        var obj_this = this;
        /* global $, JitsiMeetJS */
        var JitsiMeetJS = window['JitsiMeetJS'];
        console.log(JitsiMeetJS, 13);
        $('#meeting-room').html('');
        $('#audience').html('');
        const options = {
            hosts: {
                domain: 'meet.jit.si', //'jitsi-meet.example.com',
                //bridge: 'jitsi-videobridge.meet.jit.si',
                muc: 'conference.meet.jit.si' // FIXME: use XEP-0030
            },
            bosh: 'https://meet.jit.si/http-bind', //'//jitsi-meet.example.com/http-bind', // FIXME: use xep-0156 for that
            // The name of client node advertised in XEP-0115 'c' stanza
            //clientNode: 'http://jitsi.org/jitsimeet'            
        };

        const confOptions = {
            openBridgeChannel: true
        };

        let connection = null;
        let isJoined = false;
        let room = undefined;
        let conference = undefined;
        let roomName = undefined;

        let localTracks = [];
        const remoteTracks = {};
        var notJoined = true;
        
        $('#leave-room').click(function(){
            unload();
            obj_this.router.navigate(['/']);
        });

        let isVideo = true;
        $(window).bind('beforeunload', unload);
        $(window).bind('unload', unload);

        $('#switch_screen').click(function(){
            switchVideo()
        });

        $('body').on('click', '#audience video', function(){            
            showAsMainVideo(this.srcObject);
        });

        var meParticipant = undefined;

        // JitsiMeetJS.setLogLevel(JitsiMeetJS.logLevels.ERROR);
        const initOptions = {
            disableAudioLevels: true,

            // The ID of the jidesha extension for Chrome.
            desktopSharingChromeExtId: 'kglhbbefdnlheedjiejgomgmfplipfeb',

            // Whether desktop sharing should be disabled on Chrome.
            desktopSharingChromeDisabled: false,

            // The media sources to use when using screen sharing with the Chrome
            // extension.
            desktopSharingChromeSources: ['screen', 'window'],

            // Required version of Chrome extension
            desktopSharingChromeMinExtVersion: '0.1',

            // Whether desktop sharing should be disabled on Firefox.
            desktopSharingFirefoxDisabled: false
        };

        JitsiMeetJS.init(initOptions);

        connection = new JitsiMeetJS.JitsiConnection(null, null, options);
        //console.log(initOptions, options);
        connection.addEventListener(
            JitsiMeetJS.events.connection.CONNECTION_ESTABLISHED,
            onConnectionSuccess);
        connection.addEventListener(
            JitsiMeetJS.events.connection.CONNECTION_FAILED,
            onConnectionFailed);
        connection.addEventListener(
            JitsiMeetJS.events.connection.CONNECTION_DISCONNECTED,
            disconnect);

        JitsiMeetJS.mediaDevices.addEventListener(
            JitsiMeetJS.events.mediaDevices.DEVICE_LIST_CHANGED,
            onDeviceListChanged);
        
        connection.connect();

        JitsiMeetJS.createLocalTracks({
            devices: ['audio', 'video']
        }).then(onLocalTracks).catch(error => {
            throw error;
        });

        if (JitsiMeetJS.mediaDevices.isDeviceChangeAvailable('output')) {
            JitsiMeetJS.mediaDevices.enumerateDevices(devices => {
                const audioOutputDevices = devices.filter(d => d.kind === 'audiooutput');

                if (audioOutputDevices.length > 1) {
                    $('#audioOutputSelect').html(
                        audioOutputDevices
                        .map(
                            d =>
                            `<option value="${d.deviceId}">${d.label}</option>`)
                        .join('\n'));

                    $('#audioOutputSelectWrapper').show();
                }
            });
        }

        function showAsMainVideo(videoSource)
        {
            //console.log(videoSource);
            $('#meeting-room').html('<video autoplay/>')            
            $('#meeting-room video')[0].srcObject = videoSource;
        }

        function onLocalTracks(tracks) {
            localTracks = tracks;
            
            for (let i = 0; i < localTracks.length; i++) {
                localTracks[i].addEventListener(
                    JitsiMeetJS.events.track.TRACK_AUDIO_LEVEL_CHANGED,
                    audioLevel => console.log(`Audio Level local: ${audioLevel}`));
                localTracks[i].addEventListener(
                    JitsiMeetJS.events.track.TRACK_MUTE_CHANGED,
                    () => console.log('local track muted'));
                localTracks[i].addEventListener(
                    JitsiMeetJS.events.track.LOCAL_TRACK_STOPPED,
                    () => console.log('local track stoped'));
                localTracks[i].addEventListener(
                    JitsiMeetJS.events.track.TRACK_AUDIO_OUTPUT_CHANGED,
                    deviceId =>
                    console.log(
                        `track audio output device was changed to ${deviceId}`));
                
                var mediaHtml = '';
                var trackType = localTracks[i].getType();
                if (trackType === 'audio') {                    
                    mediaHtml = `<div class='audio'>`
                    mediaHtml += `<audio autoplay='1' id='localAudio${i}' class="localaudio" />`;
                    mediaHtml += `</div>`;
                    $('#audience').append(mediaHtml);
                    localTracks[i].attach($(`#localAudio${i}`)[0]);
                } else {
                    mediaHtml = `<div class='video'>`
                    mediaHtml += `<video autoplay='1' id='localVideo${i}' class="localvideo" />`;
                    mediaHtml += `</div>`;
                    $('#audience').append(mediaHtml);
                    localTracks[i].attach($(`#localVideo${i}`)[0]);                    
                }
                //console.log($('#meeting-room'));
                if (isJoined) {
                    room.addTrack(localTracks[i]);
                }
                if(trackType != 'video')
                {
                    $('#audience video:last').click();
                }
            }
        }        

        function onRemoteTrack(track) {            
            if (track.isLocal()) {
                return;
            }
            const participant = track.getParticipantId();
            if (!remoteTracks[participant]) {
                remoteTracks[participant] = [];
            }
            const idx = remoteTracks[participant].push(track);
            var trackType = track.getType();
            // if(trackType != 'audio')
            // {
            //     console.log(participant);
            //     console.log (track.conference.participants);
            // }

            track.addEventListener(
                JitsiMeetJS.events.track.TRACK_AUDIO_LEVEL_CHANGED,
                audioLevel => console.log(`Audio Level remote: ${audioLevel}`));
            track.addEventListener(
                JitsiMeetJS.events.track.TRACK_MUTE_CHANGED,
                () => console.log('remote track muted'));
            track.addEventListener(
                JitsiMeetJS.events.track.LOCAL_TRACK_STOPPED,
                () => console.log('remote track stoped'));
            track.addEventListener(JitsiMeetJS.events.track.TRACK_AUDIO_OUTPUT_CHANGED,
                deviceId =>
                console.log(
                    `track audio output device was changed to ${deviceId}`));
            var trackType = track.getType();
            const elementId = trackType+participant;
            var remoteHtml = '';
            
            if (track.getType() != 'audio') {
                remoteHtml = `<div class='video'><video autoplay='1' id='${elementId}' /><div>`;
                $('#audience').append(remoteHtml);
            } else {
                remoteHtml = `<div class='audio'><audio autoplay='1' id='${elementId}' /></div>`;
                $('#audience').append(remoteHtml);
            }            
            track.attach($(`#${elementId}`)[0]);
            if(trackType == 'video')
            {
                $('#audience video:last').click();                
            }            
            if(!conference)
            {                
                conference = room.e2eping.conference;
                var pinoo = conference.getPhonePin();
                console.log(pinoo ,17);
            }
        }

        function onRemoteTrackRemoved(track){
            if (track.isLocal()) {
                return;
            }
            const participant = track.getParticipantId();            
            const elementId = track.getType()+participant;
            $('#'+elementId).parent().remove();
            
            //delete participant[participant];            
            track = undefined;
        }
        
        
        function onConferenceJoined() {    
            //console.log(room.avgRtpStatsReporter._conference);
            notJoined = false;            
            isJoined = true;
            for (let i = 0; i < localTracks.length; i++) {
                room.addTrack(localTracks[i]);
            }            
            $('#leave-room').show();
        }

        function onUserLeft(id) {
            console.log('user left');
            if (!remoteTracks[id]) {
                return;
            }
            const tracks = remoteTracks[id];

            for (let i = 0; i < tracks.length; i++) {
                tracks[i].detach($(`#${id}${tracks[i].getType()}`));
            }
        }

        function onConnectionSuccess() {            
            var locs = window.location.toString();
            var arr = locs.split('/');
            locs =  arr[arr.length -1];            
            if(locs != 'jitsilow')
            {
                roomName = locs;
            }
            else
            {
                let rand = Math.floor(Math.random()*(999-100+1)+100);                
                roomName = 'dnr'+rand;
            }
            joinRoom(roomName);
        }

        function joinRoom(roomName)
        {            
            room = connection.initJitsiConference(roomName, confOptions);  
            //console.log(room);
            room.on(JitsiMeetJS.events.conference.TRACK_ADDED, onRemoteTrack);
            room.on(JitsiMeetJS.events.conference.TRACK_REMOVED, onRemoteTrackRemoved);            

            room.on(
                JitsiMeetJS.events.conference.CONFERENCE_JOINED,
                onConferenceJoined);
            
            room.on(JitsiMeetJS.events.conference.USER_JOINED, id => {                
                notJoined = false;
                $('#leave-room').show();
                console.log('user joined room '+ roomName);
                remoteTracks[id] = [];
            });
            
            room.on(JitsiMeetJS.events.conference.USER_LEFT, onUserLeft);
            
            room.on(JitsiMeetJS.events.conference.TRACK_MUTE_CHANGED, track => {
                console.log(`${track.getType()} - ${track.isMuted()}`);
            });
            room.on(
                JitsiMeetJS.events.conference.DISPLAY_NAME_CHANGED,
                (userID, displayName) => console.log(`${userID} - ${displayName}`));
            
            room.on(
                JitsiMeetJS.events.conference.TRACK_AUDIO_LEVEL_CHANGED,
                (userID, audioLevel) => console.log(`${userID} - ${audioLevel}`));
            
            room.on(
                JitsiMeetJS.events.conference.PHONE_NUMBER_CHANGED,
                () => console.log(`${room.getPhoneNumber()} - ${room.getPhonePin()}`));
            room.join();            
            setTimeout(function(){
                if(notJoined)
                console.log("Not joined")
            },6000)
        }

        function onConnectionFailed() {
            console.error('Connection Failed!');
        }

        function onDeviceListChanged(devices) {
            console.info('current devices', devices);
        }

        function disconnect() {
            console.log('disconnect!');
            connection.removeEventListener(
                JitsiMeetJS.events.connection.CONNECTION_ESTABLISHED,
                onConnectionSuccess);
            connection.removeEventListener(
                JitsiMeetJS.events.connection.CONNECTION_FAILED,
                onConnectionFailed);
            connection.removeEventListener(
                JitsiMeetJS.events.connection.CONNECTION_DISCONNECTED,
                disconnect);
        }

        function unload() {
            try{
                for (let i = 0; i < localTracks.length; i++) {
                    localTracks[i].dispose();
                }                
                //room.leave();
                connection.disconnect();                
            }
            catch(er){
                console.log(er)
            }
        }
        
        function switchVideo() { 
            // eslint-disable-line no-unused-vars
            isVideo = !isVideo;
            if (localTracks[1]) {
                localTracks[1].dispose();
                localTracks.pop();
            }
            JitsiMeetJS.createLocalTracks({
                devices: [isVideo ? 'video' : 'desktop']
            }).then(tracks => {
                localTracks.push(tracks[0]);
                localTracks[1].addEventListener(
                    JitsiMeetJS.events.track.TRACK_MUTE_CHANGED,
                    () => console.log('local track muted'));
                localTracks[1].addEventListener(
                    JitsiMeetJS.events.track.LOCAL_TRACK_STOPPED,
                    () => console.log('local track stoped'));
                localTracks[1].attach($('#localVideo1')[0]);
                room.addTrack(localTracks[1]);
            }).catch(function(error) {
                console.log("Error in screen share => ", error.message);
            });
        }

        function changeAudioOutput(selected) { 
            // eslint-disable-line no-unused-vars
            JitsiMeetJS.mediaDevices.setAudioOutputDevice(selected.value);
        }        
    }
}